    constructor(props) {
        super(props);
        this.state = {
            tt: '',
        };
    }
    componentDidMount() {
        if (!my_Bufer.buf.contact.bulen) {
            this.my_fetch_2();
        }
    }
    my_fetch_2() {
        fetch(`${my_config.url_cms}/home`, {
            method: 'GET',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
        })
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error('Something went wrong ...');
                }
            })
            .then(data => {
                console.log(data[0]);

                my_Bufer.buf.contact.store.Email = data[0].Email;
                my_Bufer.buf.contact.store.WeChat = data[0].WeChat;
                my_Bufer.buf.contact.store.WhatsApp = data[0].WhatsApp;
                my_Bufer.buf.contact.store.img = my_config.url_cms + data[0].img.url;
                my_Bufer.buf.contact.bulen = true;
                /* my_Bufer.contact */
                console.log(my_Bufer.buf.contact.store.img);
                /* this.props.state(); */
                /* this.setState({}); */
                /* my_render() */
            })
            .catch(error => {
                // console.log(error);
            });
    }
    ren;
