import React, { Component } from "react";
import {
  my_Bufer,
  my_url,
  my_stor,
  my_config
} from "../router/My_router";
import Navigator from "./block/comp/stupid_component/navigator/navigator.js";
class howThisWorks extends Component {
  constructor(props) {
    super(props);
    this.state = {
      st: [
        {
          _id: "",
          en_title: "",
          cn_title: "",
          en_video: "",
          cn_video: "",
          cn_body: "",
          en_body: "",
          ru_title: "",
          ru_body: "",
          ru_video: "",
          __v: 0,
          id: "",
          en_img: {
            url: ""
          },
          ru_img: {
            url: ""
          },
          cn_img: {
            url: ""
          }
        }
      ]
    };
  }
  componentDidMount() {
    this.my_fetch();
  }
  my_fetch() {
    fetch(my_config.url_cms + "/howThisWorks", {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
      }
    })
      .then(response => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error("Something went wrong ...");
        }
      })
      .then(data => {
        (Array.isArray(data) ? data : []).map((a, b) => {
          // console.log(my_Bufer.buf.howThisWorks.id_store);
          if (!my_Bufer.buf.howThisWorks.id_store[a._id]) {
            // console.log("&&");

            my_Bufer.buf.howThisWorks.id_store[a._id] = true;
            my_Bufer.buf.howThisWorks.st.push({
              _id: a._id,
              body: a[my_stor.lang + "_body"],
              title: a[my_stor.lang + "_title"],
              video: a[my_stor.lang + "_video"],
              img:
                a[my_stor.lang + "_img"] && a[my_stor.lang + "_img"].url
                  ? my_config.url_cms + a[my_stor.lang + "_img"].url
                  : "",
              updatedAt: a.updatedAt
            });
          }
          return "";
        });
        /* console.log(
         my_stor.lang,
          my_Bufer.buf.howThisWorks.st,
          "setState"
        ); */

        this.setState({ st: my_Bufer.buf.howThisWorks.st });
      })
      .catch(error => {
        // console.log(error);
      });
  }
  render() {
    return (
      <main className="page-content how-this-works-page">
        <div className="layout">
          <div className="layout-mid">
            <Navigator
              url_arey={[
                {
                  to: my_url(""),
                  lun_text: my_stor.page[my_stor.lang].howThisWorks.r1
                }
              ]}
              className="breadcrumbs mobile-padding"
              span={my_stor.page[my_stor.lang].howThisWorks.r2}
            />
            <div className="page-title mobile-padding">
              {this.state.st[0].title}
            </div>
            <div className="steps mobile-padding">
              <div className="step video">
                <div className="content">
                  <div className="iframe-wrapper">
                    <iframe
                      title=" "
                      width={570}
                      height={321}
                      src={this.state.st[0].video}
                      frameBorder={0}
                      allowFullScreen
                    />
                  </div>
                  <div className="text">{this.state.st[0].body}</div>
                </div>
              </div>

              {(Array.isArray(this.state.st) ? this.state.st : []).map(
                (a, b) => {
                  return (
                    <div key={b}>
                      {(() => {
                        if (b > 0) {
                          return (
                            <div className="step" key={b}>
                              <div className="title">{a.title + " - " + b}</div>
                              <div className="content">
                                <div className="image-wrapper">
                                  <img alt="" src={a.img} />
                                </div>
                                <div className="text">{a.body}</div>
                              </div>
                            </div>
                          );
                        }
                      })()}
                    </div>
                  );
                }
              )}
            </div>
          </div>
        </div>
      </main>
    );
  }
}

export default howThisWorks;



