import React, {Component} from 'react';
import update from 'react-addons-update';
import {Link} from 'react-router-dom';
import {my_Bufer, my_stor, my_config, my_history, my_fetch} from '../../../../router/My_router.js';
import News from '../stupid_component/HomePageDefault/news.js';
import Contact from '../stupid_component/HomePageDefault/contact.js';
import OurSupportIsAlwaysClose from '../stupid_component/HomePageDefault/Our_support_is_always_close.js';
import Tabl from '../stupid_component/HomePageDefault/tabl.js';
import TablMobail from '../stupid_component/HomePageDefault/tabl_mobail.js';
import Form_exchange from '../stupid_component/HomePageDefault/form_exchange.js';

class HomePageDefault extends Component {
    constructor(props) {
        super(props);
        this.state = {
            array_val: [],
            st: {
                of: [
                    {
                        forma: [
                            {
                                value: '',
                                rex: '',
                                type: '',
                                name: '',
                                text: '',
                                classname: '',
                                placeholder: '',
                                surely: false,
                            },
                            {
                                value: '',
                                rex: '',
                                type: '',
                                name: '',
                                text: '',
                                classname: '',
                                placeholder: '',
                                surely: false,
                            },
                        ],
                        name: '',
                        name_text: '',
                        as_text: '',
                        abbreviation: '',
                        img: '',
                        act: '',
                        ratio: {},
                    },
                ],
                at: [
                    {
                        forma: [
                            {
                                value: '',
                                rex: '',
                                type: '',
                                name: '',
                                text: '',
                                classname: '',
                                placeholder: '',
                                surely: false,
                            },
                            {
                                value: '',
                                rex: '',
                                type: '',
                                name: '',
                                text: '',
                                classname: '',
                                placeholder: '',
                                surely: false,
                            },
                        ],
                        name: '',
                        name_text: '',
                        as_text: '',
                        abbreviation: '',
                        img: '',
                        act: '',
                        ratio: {},
                    },
                ],
            },
            input: {
                at: '',
                of: '',
                at_class: '',
                of_class: '',
            },
            form: {
                at: '',
                of: '',
                number: 0,
                number_transf: 0,
                forma: {
                    at: {},
                    of: {},
                    summ: {
                        sent: {
                            name: '',
                            value: '',
                            abbreviation: '',
                        },
                        expected: {
                            name: '',
                            value: '',
                            abbreviation: '',
                        },
                    },
                },
            },
            error: {},
        };
        this.my_state = this.my_state.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.change_exchange = this.change_exchange.bind(this);
    }
    //  getInitialState(){
    //    return()
    //  }
    componentWillMount() {
        let arr = [
            {
                forma: [
                    {
                        value: '',
                        rex: '',
                        type: '',
                        name: '',
                        text: '',
                        classname: '',
                        placeholder: '',
                        surely: false,
                    },
                    {
                        value: '',
                        rex: '',
                        type: '',
                        name: '',
                        text: '',
                        classname: '',
                        placeholder: '',
                        surely: false,
                    },
                ],
                name: '',
                name_text: '',
                as_text: '',
                abbreviation: '',
                img: '',
                act: '',
                ratio: {},
                user: {},
            },
        ];
        let obj = {
            of: arr,
            at: arr,
        };
        this.state.st = Object.assign({}, obj);
    }
    change_exchange() {
        this.state.st.of.map((a, b) => {
            if (this.state.st.of[b].name === this.state.form.of) {
                this.state.st.of[b].user[this.state.form.of] = this.state.form.number;
                for (let a_1 = 0, c_1 = this.state.st.at.length; a_1 < c_1; a_1++) {
                    this.state.st.at[a_1].user[this.state.form.of] =
                        Number(this.state.form.number) * Number(this.state.st.at[a_1].ratio[this.state.form.of]);
                    if (this.state.form.at === this.state.st.at[a_1].name) {
                        this.state.form.number_transf = this.state.st.at[a_1].user[this.state.form.of];
                    }
                }
            } else {
                this.state.st.of[b].user[this.state.form.of] = this.state.st.of[b].ratio[this.state.form.of];
            }
        });

        this.setState({}, console.log(this.state));
    }
    my_state() {
        this.setState({});
    }
    componentDidMount() {
        my_fetch({
            name: 'Payment_reg',
            url: 'https://cms.fin.so/Currencies?name=сourse',
            method: 'GET',
            fun: data => {
                this.state.st = data[0].JSON;

                this.state.st.at.map((a, b) => {
                    this.state.st.at[b].user = Object.assign({}, this.state.st.at[b].ratio);
                    for (let a_1 in this.state.st.at[b].user) {
                        this.state.st.at[b].user[a_1] = 0;
                    }
                });
                this.state.st.of.map((a, b) => {
                    this.state.st.of[b].user = Object.assign({}, this.state.st.of[b].ratio);
                });
                this.state.input.of_class = this.state.st.of[0].name;
                this.state.form.of = this.state.st.of[0].name;
                this.my_fetch();
            },
        });
    }
    my_fetch(data) {
        data = data || 0;
        fetch(my_config.url_cms + `/news?_sort=updatedAt:desc&_start=0&_limit=3`, {
            method: 'GET',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
        })
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error('Something went wrong ...');
                }
            })
            .then(data => {
                (Array.isArray(data) ? data : []).map((a, b) => {
                    //  console.log(my_Bufer.news.id_store);
                    if (!my_Bufer.buf.news.id_store[a._id]) {
                        // console.log("&&");
                        my_Bufer.buf.news.id_store[a._id] = true;
                        my_Bufer.buf.news.st.push({
                            _id: a._id,
                            body: a[my_stor.lang + '_body'],
                            title: a[my_stor.lang + '_title'],
                            img:
                                a[my_stor.lang + '_img'] && a[my_stor.lang + '_img'].url
                                    ? my_config.url_cms + a[my_stor.lang + '_img'].url
                                    : '',
                            updatedAt: a.updatedAt,
                        });
                    }
                    return '';
                });
                // console.logmy_stor.lang,my_Bufer.news.st,"setState");

                this.setState({});
            })
            .catch(error => {
                // console.log(error);
            });
    }
    handleChange(e) {
        const Change = {
            item_search: () => {
                this.setState({
                    /**
                     * change field for search
                     */
                    input: update(this.state.input, {
                        [e.target.dataset.act]: {$set: e.target.value},
                    }),
                });
            },
            currency_selection: () => {
                /** class change*/
                this.setState({
                    input: update(this.state.input, {
                        [`${e.target.dataset.input}_class`]: {$set: e.target.value},
                    }),
                    /**form filling*/
                    form: update(this.state.form, {
                        [e.target.dataset.input]: {$set: e.target.value},
                    }),
                });
            },
            change_number: () => {
                if (!e.target.value || /^[0-9]{1,20}(\,|\.)?([0-9]{1,20})?$/gim.test(e.target.value)) {
                    e.target.value = e.target.value.replace(/\,/gim, '.');
                    this.setState(
                        {
                            form: update(this.state.form, {
                                number: {$set: e.target.value},
                            }),
                        },
                        this.change_exchange,
                    );
                }
            },
            form_exchange: () => {
                for (let a = 0, c = this.state.st[e.target.dataset.input].length; a < c; a++) {
                    if (this.state.st[e.target.dataset.input][a].name === e.target.dataset.name_general) {
                        for (
                            let a_1 = 0, c_1 = this.state.st[e.target.dataset.input][a].forma.length;
                            a_1 < c_1;
                            a_1++
                        ) {
                            if (this.state.st[e.target.dataset.input][a].forma[a_1].name === e.target.dataset.name) {
                                this.setState({
                                    st: update(this.state.st, {
                                        [e.target.dataset.input]: {
                                            [a]: {forma: {[a_1]: {value: {$set: e.target.value}}}},
                                        },
                                    }),
                                });
                                break;
                            }
                        }
                        break;
                    }
                }
            },
        };
        if (Change[e.target.dataset.change]) {
            Change[e.target.dataset.change]();
        } else {
            console.error('e.target.dataset.change');
        }

        /* this.setState({at: e.target.value}); */
    }
    handleClick(e) {
        const Click = {
            currency_selection: () => {
                this.setState(
                    {
                        input: update(this.state.input, {
                            [`${e.target.dataset.input}_class`]: {$set: e.target.dataset.name},
                        }),
                        form: update(this.state.form, {
                            [e.target.dataset.input]: {$set: e.target.dataset.name},
                        }),
                    },
                    this.change_exchange,
                );
            },
            form_exchange: () => {
                let FLAG = [false, false];
                for (let a = 0, c = this.state.st.of.length; a < c; a++) {
                    if (this.state.st.of[a].name === e.target.dataset.of) {
                        this.state.form.forma.of = Object.assign({}, this.state.st.of[a].forma);
                        FLAG[0] = true;
                        break;
                    }
                }
                for (let a = 0, c = this.state.st.at.length; a < c; a++) {
                    if (this.state.st.at[a].name === e.target.dataset.at) {
                        this.state.form.forma.at = Object.assign({}, this.state.st.at[a].forma);
                        FLAG[1] = true;
                        break;
                    }
                }
                this.state.form.forma.summ.sent.name = e.target.dataset.of;
                this.state.form.forma.summ.sent.value = Number(this.state.form.number);
                this.state.form.forma.summ.expected.name = e.target.dataset.at;
                this.state.form.forma.summ.expected.value = Number(this.state.form.number_transf);
                if (FLAG[0] && FLAG[1]) {
                    my_fetch({
                        name: 'Payment_reg',
                        url: 'https://cms.fin.so/Currencies?name=expected',
                        method: 'GET',
                        fun: data => {
                            my_history.push('/Payment');
                        },
                    });
                } else {
                }
            },
        };
        if (Click[e.target.dataset.сlick]) {
            Click[e.target.dataset.сlick]();
        } else {
            console.error('e.target.dataset.сlick');
        }
    }
    render() {
        return (
            <main className="page-content home-page">
                <div className="background" />
                <div className="layout">
                    <div className="layout-mid with-sides">
                        <div className="side-left">
                            <div className="box">
                                {
                                    <TablMobail
                                        let
                                        placeholder_1={my_stor.page[my_stor.lang].HomePageDefault.r2}
                                        placeholder_2={my_stor.page[my_stor.lang].HomePageDefault.r2}
                                        m_item={{str: this.state}}
                                        m_title={my_stor.page[my_stor.lang].HomePageDefault.r64}
                                        m_title_2={my_stor.page[my_stor.lang].HomePageDefault.r3}
                                        text_1={my_stor.page[my_stor.lang].HomePageDefault.r1}
                                        text_2={my_stor.page[my_stor.lang].HomePageDefault.r50}
                                        text_3={my_stor.page[my_stor.lang].HomePageDefault.r51}
                                        text_4={my_stor.page[my_stor.lang].HomePageDefault.r52}
                                        text_5={my_stor.page[my_stor.lang].HomePageDefault.r4}
                                        text_6={my_stor.page[my_stor.lang].HomePageDefault.r5}
                                        text_7={my_stor.page[my_stor.lang].HomePageDefault.r6}
                                        text_8={my_stor.page[my_stor.lang].HomePageDefault.r7}
                                        handleChange={this.handleChange}
                                        handleClick={this.handleClick}
                                    />
                                }
                                <input
                                    type="radio"
                                    name="exchange-left-currency"
                                    id="exchange-left-currency-default"
                                    defaultChecked
                                    onChange={this.handleChange}
                                    onClick={this.handleClick}
                                />
                                <Tabl
                                    m_item={{str: this.state}}
                                    handleChange={this.handleChange}
                                    handleClick={this.handleClick}
                                />
                            </div>
                        </div>
                        <div className="side-right">
                            <div className="search-request tablet-left">
                                <div className="text">{my_stor.page[my_stor.lang].HomePageDefault.r42}</div>
                                <form>
                                    <input
                                        type="text"
                                        className="dark"
                                        placeholder={my_stor.page[my_stor.lang].HomePageDefault.r47 + '3851321'}
                                    />
                                    <button type="submit" />
                                </form>
                            </div>
                            <div className="side-banner tablet-right">
                                <img alt="" src={my_Bufer.buf.contact.store.img} />
                            </div>

                            <OurSupportIsAlwaysClose language={my_stor.page[my_stor.lang]} />
                            <div className="support tablet-right">
                                <Contact
                                    home="true"
                                    state={() => {
                                        this.my_state();
                                    }}
                                />
                            </div>
                        </div>
                        <div className="clear" />
                        <div className="text-content">
                            <div className="title">{my_stor.page[my_stor.lang].HomePageDefault.r37}</div>
                            <div className="text" data-pageame=".HomePageDefault.r36">
                                {my_stor.page[my_stor.lang].HomePageDefault.r36}
                            </div>
                        </div>
                        <News
                            text_1={my_stor.page[my_stor.lang].HomePageDefault.r38}
                            text_2={my_stor.page[my_stor.lang].HomePageDefault.r41}
                            st={my_Bufer.buf.news.st}
                        />
                        <div className="clear" />
                    </div>
                </div>
            </main>
        );
    }
}

export default HomePageDefault;



