import React, { Component } from "react";
 import {  Link } from "react-router-dom";
// import {  my_url, my_stor } from "../../../../../router/My_router";

class App extends Component {
  render() {
    return (
      <div className="news">
      <div className="header">
        {/* {my_stor.page[my_stor.lang].HomePageDefault.r38} */}
        {this.props.text_1}
      </div>
      <div className="items">
        {(Array.isArray(this.props.st) ? this.props.st : []).map(
          (a, b) => {
            return (
              <div className="item" key={b}>
                <div className="date">
                  {new Date(a.updatedAt).getDate() +
                    "-" +
                    (new Date(a.updatedAt).getMonth() + 1) +
                    "-" +
                    new Date(a.updatedAt).getFullYear()}
                </div>
                <div className="text">{a.title}</div>
              </div>
            );
          }
        )}
        <div className="all-items">
          <Link to="/News">
            {/* {my_stor.page[my_stor.lang].HomePageDefault.r41} */}
            {this.props.text_2}
          </Link>
        </div>
      </div>
    </div>
    );
  }
}



export default App;



