import React, { Component } from "react";
import {  Link } from "react-router-dom";
import { my_Bufer,my_config, my_render, my_stor,my_BuferSTANDART } from "../router/My_router";
let obj_Language;
class lang extends Component {
  /* constructor(props) {
    super(props)
      this.state = { lang: "lang" };
  } */
  componentDidMount(lang) {
    lang=lang||my_stor.lang
    console.log("componentDidMount")
    if (my_config.lang_click||!my_config.lang_render) {
      my_config.lang_click=false
      my_config.lang_render=true
      this.my_fetch(lang);
    } 
  }
  my_fetch(lang) {

    fetch(
     my_config.url_cms + `/language?Language=` +lang,
      {
        method: "GET",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        }
      }
    )
      .then(response => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error("Something went wrong ...");
        }
      })
      .then(data => {
        obj_Language = data[0];
        this.my_map();
         
      })
      .catch(error => {
        console.error(error);
      });
  }
  my_map() {
    // console.log("my_map()");
    (Array.isArray(Object.keys(obj_Language))?Object.keys(obj_Language):[]).map((a, b) => {
      if (my_config.name_page[a]) {
        try {
           let pege_lan=JSON.parse(obj_Language[a].replace(/\\|\u00A0/gmi, '').trim());
          (Array.isArray(Object.keys(pege_lan))?Object.keys(pege_lan):[]).map((a_1,b_1)=>{
            if (/\]$/gmi.test(pege_lan[a_1])) {
              pege_lan[a_1]=JSON.parse(pege_lan[a_1]);
            } else {
             my_stor.page[my_stor.lang][a][a_1]=pege_lan[a_1];
            }
            return ""
          });
          window.localStorage.setItem("userLang_ae_fin_obj",JSON.stringify( my_stor))
        } catch (error) {
          console.error(a,error);
          
        }
        
        // console.log(dat[a]);
      }
      return ""
    });
    my_render();
  }
  my_handleClick(e) {
    if (my_stor.lang!==e) {
      my_config.lang_click=true
      my_Bufer.buf=JSON.parse(my_BuferSTANDART)
      this.componentDidMount(e);
      my_stor.lang = e;
    }
  }
  render(match) {

    
    return (
      <div className="lang">
        <div className="lang-select">
          <div className="selected">
            <label htmlFor="header-lang" className="link white">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                xmlnsXlink="http://www.w3.org/1999/xlink"
                width={17}
                height={17}
                viewBox="0 0 17 17"
              >
                <defs>
                  <path id="a" d="M0 0h16.019v16.019H0z" />
                </defs>
                <g fill="none" fillRule="evenodd">
                  <mask id="b" fill="#fff">
                    <use xlinkHref="#a" />
                  </mask>
                  <use fill="#FFDA44" xlinkHref="#a" />
                  <g fillRule="nonzero" mask="url(#b)">
                    <g transform="translate(-6.408 -3.204)">
                      <ellipse
                        cx="14.818"
                        cy="11.614"
                        fill="#F0F0F0"
                        rx="13.216"
                        ry="11.614"
                      />
                      <path
                        fill="#0052B4"
                        d="M27.261 14c.5-.868.772-1.807.772-2.787s-.273-1.918-.772-2.786H3.976c-.499.868-.772 1.807-.772 2.786 0 .98.273 1.919.772 2.786l11.643.697 11.642-.697z"
                      />
                      <path
                        fill="#D80027"
                        d="M14.017 23.228c6.426 0 11.905-3.84 14.016-9.229H0c2.112 5.389 7.59 9.229 14.017 9.229z"
                      />
                    </g>
                  </g>
                </g>
              </svg>
              {my_stor.lang
                ?my_stor.lang.toUpperCase()
                :my_stor.lang}
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width={14}
                height={8}
                viewBox="0 0 14 8"
              >
                <path
                  fill="#FFF"
                  fillRule="nonzero"
                  d="M1.101.377l-.713.693 6.204 6.026.356.332.357-.332L13.51 1.07l-.714-.693-5.847 5.68z"
                />
              </svg>
            </label>
          </div>
          <input id="header-lang" type="checkbox" />
          <div className="options">

            {(my_stor&&my_stor.lang_array&&Array.isArray(my_stor.lang_array)?my_stor.lang_array:[]).map((a, b) => {
              if (a !==my_stor.lang) {
                return (
                  <Link
                    to="#"
                    className="item"
                    onClick={() => this.my_handleClick(a)}
                    key={b}
                  >
                    <div className="white">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        xmlnsXlink="http://www.w3.org/1999/xlink"
                        width={17}
                        height={17}
                        viewBox="0 0 17 17"
                      >
                        <defs>
                          <path id="a" d="M0 0h16.019v16.019H0z" />
                        </defs>
                        <g fill="none" fillRule="evenodd">
                          <mask id="b" fill="#fff">
                            <use xlinkHref="#a" />
                          </mask>
                          <use fill="#FFDA44" xlinkHref="#a" />
                          <g fillRule="nonzero" mask="url(#b)">
                            <g transform="translate(-6.408 -3.204)">
                              <ellipse
                                cx="14.818"
                                cy="11.614"
                                fill="#F0F0F0"
                                rx="13.216"
                                ry="11.614"
                              />
                              <path
                                fill="#0052B4"
                                d="M27.261 14c.5-.868.772-1.807.772-2.787s-.273-1.918-.772-2.786H3.976c-.499.868-.772 1.807-.772 2.786 0 .98.273 1.919.772 2.786l11.643.697 11.642-.697z"
                              />
                              <path
                                fill="#D80027"
                                d="M14.017 23.228c6.426 0 11.905-3.84 14.016-9.229H0c2.112 5.389 7.59 9.229 14.017 9.229z"
                              />
                            </g>
                          </g>
                        </g>
                      </svg>
                      {a.toUpperCase()}
                    </div>
                  </Link>
                );
              } else {
                return "";
              }
            })}
          </div>
        </div>
      </div>
    );
  }
}
export default lang;



