import React, {Component} from 'react';
// /* import {  Link } from "react-router-dom";
// import {  my_url, my_stor } from "../../../../../router/My_router";

class App extends Component {
    render() {
        return (
            <div className="exchange-form">
                <div className="text_exchange">
                    <div className="course">Курс обмена: xxx = xxx руб.</div>
                    <div className="text">Тариф обмена:</div>
                    <div className="radios">
                        <label htmlFor="exchange-form-rate-fast" className="radio dark">
                            Быстрый
                        </label>
                        <label htmlFor="exchange-form-rate-eco" className="radio dark checked">
                            Эконом
                        </label>
                    </div>
                    <div className="time">
                        Ожидаемое время обмена:<br />x минут
                    </div>
                </div>
                <div className="form_exchange my_class_1">
                    <div className="inputs-line-column  my_class_2">
                        {this.props.m_item.form.of && this.props.m_item.form.at
                            ? this.props.m_item.st.of.map((a, b) => {
                                  if (this.props.m_item.st.of[b].name === this.props.m_item.form.of) {
                                      return this.props.m_item.st.of[b].forma.map((a_1, b_1) => {
                                          return (
                                              <div key={b_1} className="inputs-line">
                                                  <input
                                                      data-input="of"
                                                      data-change="form_exchange"
                                                      data-name={a_1.name}
                                                      data-name_general={this.props.m_item.form.of}
                                                      value={a_1.value}
                                                      onChange={this.props.onChange}
                                                      type={a_1.type}
                                                      className="transparent"
                                                      placeholder={a_1.placeholder}
                                                  />
                                              </div>
                                          );
                                      });
                                  } else {
                                      return '';
                                  }
                              })
                            : ''}
                    </div>
                    <div className="inputs-line-column my_class_3">
                        {this.props.m_item.form.of && this.props.m_item.form.at
                            ? this.props.m_item.st.at.map((a, b) => {
                                  if (this.props.m_item.st.at[b].name === this.props.m_item.form.at) {
                                      return this.props.m_item.st.at[b].forma.map((a_1, b_1) => {
                                          return (
                                              <div key={b_1} className="inputs-line">
                                                  <input
                                                      data-input="at"
                                                      data-change="form_exchange"
                                                      data-name={a_1.name}
                                                      data-name_general={this.props.m_item.form.at}
                                                      value={a_1.value}
                                                      onChange={this.props.onChange}
                                                      type={a_1.type}
                                                      className="transparent"
                                                      placeholder={a_1.placeholder}
                                                  />
                                              </div>
                                          );
                                      });
                                  } else {
                                      return '';
                                  }
                              })
                            : ''}
                    </div>
                </div>
                <div className="inputs-line my_class_4">
                    <div className="inputs-line-column">
                        <button
                            type="submit"
                            className="button"
                            data-at={this.props.m_item.form.at}
                            data-of={this.props.m_item.form.of}
                            data-сlick="form_exchange"
                            onClick={this.props.onClick}
                        >
                            Перейти к оплате
                        </button>
                    </div>
                </div>
            </div>
        );
    }
}

export default App;



