import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {my_Bufer, my_url, my_stor, my_config} from '../router/My_router';
import Navigator from './block/comp/stupid_component/navigator/navigator.js';

// my_Bufer.news={};
// my_Bufer.news={id_store:{},st: [], len: 10, my_var: 10 };
// console.log(My_router,"My_router");
class News extends Component {
    constructor(props) {
        super(props);
        this.state = {st: []};
    }
    componentDidMount() {
        this.my_fetch();
    }
    my_fetch(data) {
        data = data || 0;
        fetch(my_config.url_cms + `/news?_sort=updatedAt:desc&_start=${data}&_limit=10`, {
            method: 'GET',
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
        })
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    throw new Error('Something went wrong ...');
                }
            })
            .then(data => {
                (Array.isArray(data) ? data : []).map((a, b) => {
                    //  console.log(my_Bufer.news.id_store);
                    if (!my_Bufer.buf.news.id_store[a._id]) {
                        // console.log("&&");

                        my_Bufer.buf.news.id_store[a._id] = true;
                        my_Bufer.buf.news.st.push({
                            _id: a._id,
                            body: a[my_stor.lang + '_body'],
                            title: a[my_stor.lang + '_title'],
                            img:
                                a[my_stor.lang + '_img'] && a[my_stor.lang + '_img'].url
                                    ? my_config.url_cms + a[my_stor.lang + '_img'].url
                                    : '',
                            updatedAt: a.updatedAt,
                        });
                    }
                    return '';
                });
                // console.logmy_stor.lang,my_Bufer.news.st,"setState");

                this.setState({st: my_Bufer.buf.news.st});
            })
            .catch(error => {
                // console.log(error);
            });
    }
    render() {
        // console.log(this, "news");
        return (
            <main className="page-content news-page">
                <div className="layout">
                    <div className="layout-mid">
                        <Navigator
                            url_arey={[
                                {
                                    to: my_url(''),
                                    lun_text: my_stor.page[my_stor.lang].News.r1,
                                },
                            ]}
                            className="breadcrumbs mobile-padding"
                            span={my_stor.page[my_stor.lang].News.r2}
                        />
                        <div className="page-title mobile-padding" data-pageame="News.r2">
                            {my_stor.page[my_stor.lang].News.r2}
                        </div>
                    </div>
                </div>
                <div className="layout mobile-padding">
                    <div className="layout-mid">
                        <div className="news-list">
                            {/* {console.log(news,"news")} */}
                            {(Array.isArray(this.state.st) ? this.state.st : []).map((a, b) => {
                                // console.log(a);
                                return (
                                    <div className="item" key={b}>
                                        <div>
                                            <img alt="" src={a.img} />
                                        </div>
                                        <div className="content">
                                            <div className="date">
                                                <svg
                                                    xmlns="http://www.w3.org/2000/svg"
                                                    width={22}
                                                    height={23}
                                                    viewBox="0 0 22 23"
                                                >
                                                    <path
                                                        fill="#B4B4B7"
                                                        fillRule="nonzero"
                                                        d="M4 0v1H0v22h22V1h-4V0h-2v1H6V0H4zM2 3h2v1h2V3h10v1h2V3h2v2H2V3zm0 4h18v14H2V7zm6 2v2h2V9H8zm4 0v2h2V9h-2zm4 0v2h2V9h-2zM4 13v2h2v-2H4zm4 0v2h2v-2H8zm4 0v2h2v-2h-2zm4 0v2h2v-2h-2zM4 17v2h2v-2H4zm4 0v2h2v-2H8zm4 0v2h2v-2h-2z"
                                                    />
                                                </svg>
                                            </div>
                                            <div className="title">{a.title}</div>
                                            <div className="text">{a.body}</div>
                                            <div className="more-info">
                                                <Link to="#" className="more-info" data-pageame="News.r5">
                                                    {' '}
                                                    {my_stor.page[my_stor.lang].News.r5} &gt;
                                                </Link>
                                            </div>
                                        </div>
                                        <div className="clear" />
                                    </div>
                                );
                            })}
                        </div>
                        <div className="more-items">
                            {(() => {
                                if (this.state.st.length >= my_Bufer.buf.news.len) {
                                    my_Bufer.buf.news.len = my_Bufer.news.len + my_Bufer.buf.news.my_var;
                                    return (
                                        <Link
                                            to="#"
                                            className="button white"
                                            onClick={() => {
                                                this.my_fetch(my_Bufer.buf.news.len - my_Bufer.news.my_var);
                                            }}
                                            data-pageame="News.r6"
                                        >
                                            {my_stor.page[my_stor.lang].News.r6}
                                        </Link>
                                    );
                                } else {
                                    return '';
                                }
                            })()}
                        </div>
                    </div>
                </div>
            </main>
        );
    }
}

export default News;



