import React, {Component} from 'react';
import {Link} from 'react-router-dom';
// import {   my_stor } from "../../../../../router/My_router";
// import Item from "./items.js";

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            st: 'l1' + this.props.id,
            items: '',
            currency: 'all',
        };
    }
    render() {
        let placeholder_1 = this.props.placeholder_1 || '',
            placeholder_2 = this.props.placeholder_2 || '',
            m_item = this.props.m_item || [],
            m_title = this.props.m_title || '',
            m_title_2 = this.props.m_title_2 || '',
            text_1 = this.props.text_1 || '',
            text_2 = this.props.text_2 || '',
            text_3 = this.props.text_3 || '',
            text_4 = this.props.text_4 || '',
            text_5 = this.props.text_5 || '',
            text_6 = this.props.text_6 || '',
            text_7 = this.props.text_7 || '',
            text_8 = this.props.text_7 || '';

        return (
            <div className="header-columns">
                <div className="columns">
                    <div className="column left">
                        <div className="header">
                            <div className="img">
                                <svg xmlns="http://www.w3.org/2000/svg" width={28} height={28} viewBox="0 0 28 28">
                                    <g fill="none" fillRule="evenodd">
                                        <path
                                            fill="#FFF"
                                            fillRule="nonzero"
                                            d="M9.82 0C6.035 0 2.946 3.09 2.946 6.874a6.9 6.9 0 0 0 3.038 5.708C2.481 14.084 0 17.56 0 21.604h1.964a7.84 7.84 0 0 1 7.856-7.856c1.351 0 2.607.352 3.713.951a7.86 7.86 0 0 0-1.749 4.941c0 4.327 3.53 7.856 7.856 7.856 4.327 0 7.856-3.529 7.856-7.856s-3.529-7.856-7.856-7.856a7.826 7.826 0 0 0-4.603 1.504 9.964 9.964 0 0 0-1.38-.706 6.9 6.9 0 0 0 3.037-5.708C16.694 3.09 13.604 0 9.82 0zm0 1.964a4.895 4.895 0 0 1 4.91 4.91 4.895 4.895 0 0 1-4.91 4.91 4.895 4.895 0 0 1-4.91-4.91 4.895 4.895 0 0 1 4.91-4.91zm9.82 11.784a5.877 5.877 0 0 1 5.892 5.892 5.877 5.877 0 0 1-5.892 5.892 5.877 5.877 0 0 1-5.892-5.892 5.877 5.877 0 0 1 5.892-5.892z"
                                        />
                                        <g stroke="#FFF" strokeWidth={2}>
                                            <path d="M16.426 19.58H22.9M19.662 22.868l3.184-3.303-3.184-3.23" />
                                        </g>
                                    </g>
                                </svg>
                            </div>
                            <div className="title">{m_title}</div>
                            <input
                                type="text"
                                className={`dark${m_item.str.err.class['change_number']}` ? 'error_item' : ''}
                                placeholder={text_1}
                                onChange={this.props.handleChange}
                                data-change="change_number"
                                value={m_item.str.form.number !== 0 ? m_item.str.form.number : ''}
                            />
                            {/* <div className="currency-title">BTC</div> */}
                        </div>

                        <div className="mobile-select">
                            <select
                                className="with-icon"
                                data-placeholder={placeholder_2}
                                onChange={this.props.handleChange}
                                value={m_item.str.form.of}
                                data-input="of"
                                data-tablmobail="true"
                                data-change="currency_selection"
                            >
                                <option disabled value="" />
                                {m_item.str.st['of'].map((a, b) => {
                                    let flag = false;
                                    for (let a_1 = 0, c_1 = m_item.str.st['at'].length; a_1 < c_1; a_1++) {
                                        if (m_item.str.st['at'][a_1].ratio[a.name]) {
                                            flag = true;
                                            break;
                                        }
                                    }
                                    if (flag) {
                                        return (
                                            <option key={b} value={a.name}>
                                                {a.name_text}
                                            </option>
                                        );
                                    } else {
                                        return '';
                                    }
                                })}
                            </select>
                        </div>
                        <div className="mobile-rate" data-pageame=".HomePageDefault.r50">
                            {text_2}1 btc. = xxx
                            {text_3}.
                        </div>
                    </div>
                    <div className="column right">
                        <div className="header">
                            <div className="img">
                                <svg xmlns="http://www.w3.org/2000/svg" width={28} height={28} viewBox="0 0 28 28">
                                    <g fill="none" fillRule="evenodd">
                                        <path
                                            fill="#FFF"
                                            fillRule="nonzero"
                                            d="M18.18 0c3.785 0 6.874 3.09 6.874 6.874a6.9 6.9 0 0 1-3.038 5.708C25.519 14.084 28 17.56 28 21.604h-1.964a7.84 7.84 0 0 0-7.856-7.856 7.748 7.748 0 0 0-3.713.951 7.86 7.86 0 0 1 1.749 4.941c0 4.327-3.53 7.856-7.856 7.856-4.327 0-7.856-3.529-7.856-7.856s3.529-7.856 7.856-7.856c1.715 0 3.308.564 4.603 1.504a9.964 9.964 0 0 1 1.38-.706 6.9 6.9 0 0 1-3.037-5.708C11.306 3.09 14.396 0 18.18 0zm0 1.964a4.895 4.895 0 0 0-4.91 4.91 4.895 4.895 0 0 0 4.91 4.91 4.895 4.895 0 0 0 4.91-4.91 4.895 4.895 0 0 0-4.91-4.91zM8.36 13.748a5.877 5.877 0 0 0-5.892 5.892 5.877 5.877 0 0 0 5.892 5.892 5.877 5.877 0 0 0 5.892-5.892 5.877 5.877 0 0 0-5.892-5.892z"
                                        />
                                        <g stroke="#FFF" strokeWidth={2}>
                                            <path d="M4.363 19.58h6.476M7.6 16.292l3.184 3.302-3.184 3.23" />
                                        </g>
                                    </g>
                                </svg>
                            </div>
                            <div className="title">{m_title_2}</div>
                            <input
                                type="text"
                                className="dark"
                                placeholder={text_4}
                                value={m_item.str.form.number_transf !== 0 ? m_item.str.form.number_transf : ''}
                                disabled
                            />
                        </div>
                        <div className="mobile-select">
                            <select
                                className="with-icon"
                                data-placeholder={placeholder_1}
                                onChange={this.props.handleChange}
                                value={m_item.str.form.at}
                                data-input="at"
                                data-tablmobail="true"
                                data-change="currency_selection"
                            >
                                <option disabled value="" />
                                {m_item.str.st['at'].map((a, b) => {
                                    let flag = false;
                                    for (let a_1 = 0, c_1 = m_item.str.st['of'].length; a_1 < c_1; a_1++) {
                                        if (m_item.str.st['of'][a_1].ratio[a.name]) {
                                            flag = true;
                                            break;
                                        }
                                    }
                                    if (flag) {
                                        return (
                                            <option key={b} value={a.name}>
                                                {a.name_text}
                                            </option>
                                        );
                                    } else {
                                        return '';
                                    }
                                })}
                            </select>
                        </div>
                    </div>
                    <div className="mobile-submit">
                        <button type="submit" className="button">
                            {text_5}
                        </button>
                    </div>
                </div>
                <div className="under-columns" data-pageame=".HomePageDefault.r5">
                    {text_6}
                    <br />
                    <Link to="#" className="yellow">
                        {text_7}
                    </Link>,{text_8}
                </div>
            </div>
        );
    }
}

export default App;



