import React, { Component } from 'react';
import {  Link } from "react-router-dom";
import {  my_url, my_stor }  from '../router/My_router';
class footer extends Component {
  render() {
    return (
      <footer>
        <div className="layout">
          <div className="layout-mid">
            <div className="text">
              © SiteName 2014-2015<br />
              {my_stor.page[my_stor.lang].footer.r1}
            </div>
            <div className="links">
              <div className="link">
                <Link to={my_url("/howThisWorks")} >{my_stor.page[my_stor.lang].footer.r2}</Link>
              </div>
              <div className="link">
                <Link to={my_url("/Cooperation")} >{my_stor.page[my_stor.lang].footer.r3  }</Link>
              </div>
              <div className="link">
              <Link to={my_url("/News")} >{my_stor.page[my_stor.lang].footer.r4}</Link>
              </div>
              <div className="link">
              <Link to= {my_url("/Contacts")}>{my_stor.page[my_stor.lang].footer.r5  }</Link>
              </div>
              <div className="link">
              <Link to={my_url("/faq")} >{my_stor.page[my_stor.lang].footer.r6  }</Link>
              </div>
            </div>
            <div className="clear" />
          </div>
        </div>
      </footer>
    );
  }
}

export default footer;



