import React, {Component} from 'react';

import {Link} from 'react-router-dom';
import {my_stor} from '../../../../../router/My_router';
import Item from './items.js';
import Form_exchange from './form_exchange.js';
class App extends Component {
    render() {
        let handleChange = this.props.handleChange,
            m_item = this.props.m_item || [];
        return (
            <div className={`tables ${'with-form'}`}>
                <div className="columns">
                    <div className="column left">
                        <div className="table">
                            <div className="tabs">
                                <div className="tab">
                                    <div
                                        className={`tab-content ${this.props.m_item.str.form.at &&
                                        this.props.m_item.str.form.at
                                            ? 'my_tab-content'
                                            : 'my_height_tabl'} `}
                                    >
                                        <div className="exchange-currency" />
                                        <div className="search-currency">
                                            {
                                                <input
                                                    type="text"
                                                    placeholder={my_stor.page[my_stor.lang].HomePageDefault.r29}
                                                    onChange={this.props.handleChange}
                                                    value={m_item.str.input['of']}
                                                    data-act={'of'}
                                                    data-change="item_search"
                                                />
                                            }
                                        </div>
                                        <br />
                                        <div className="column-title">
                                            {my_stor.page[my_stor.lang].HomePageDefault.r14}
                                        </div>
                                        <div className="scrollbar">
                                            <div className="items">
                                                {m_item.str.st['of'].map((a, b) => {
                                                    let test = (
                                                        <Item
                                                            key={b}
                                                            src={a.img}
                                                            text_1={a.name_text}
                                                            text_2={a.as_text}
                                                            number={a.user[m_item.str.form['of']]}
                                                            name={a.name}
                                                            input="of"
                                                            activ_class={m_item.str.input.of_class}
                                                            handleClick={this.props.handleClick}
                                                        />
                                                    );
                                                    if (
                                                        !m_item.str.input['of'] ||
                                                        a.name_text
                                                            .toLowerCase()
                                                            .indexOf(m_item.str.input['of'].toLowerCase()) !== -1
                                                    ) {
                                                        let flag = false;
                                                        for (
                                                            let a_1 = 0, c_1 = m_item.str.st['at'].length;
                                                            a_1 < c_1;
                                                            a_1++
                                                        ) {
                                                            if (
                                                                !m_item.str.input['at'] ||
                                                                m_item.str.st['at'][a_1].name_text
                                                                    .toLowerCase()
                                                                    .indexOf(m_item.str.input['at'].toLowerCase()) !==
                                                                    -1
                                                            ) {
                                                                if (m_item.str.st['of'][a_1].ratio[a.name]) {
                                                                    flag = true;
                                                                    break;
                                                                }
                                                            }
                                                        }
                                                        if (flag) {
                                                            return test;
                                                        } else {
                                                            return '';
                                                        }
                                                    }
                                                    return '';
                                                })}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="column right">
                        <div className="table">
                            <div className="tabs">
                                <div className="tab">
                                    <div
                                        className={`tab-content ${this.props.m_item.str.form.at &&
                                        this.props.m_item.str.form.at
                                            ? 'my_tab-content'
                                            : 'my_height_tabl'} `}
                                    >
                                        <div className="exchange-currency" />
                                        <div className="search-currency">
                                            {
                                                <input
                                                    type="text"
                                                    placeholder={my_stor.page[my_stor.lang].HomePageDefault.r29}
                                                    onChange={handleChange}
                                                    value={m_item.str.input['at']}
                                                    data-act={'at'}
                                                    data-change="item_search"
                                                />
                                            }
                                        </div>
                                        <br />
                                        <div className="column-title">
                                            {my_stor.page[my_stor.lang].HomePageDefault.r30}
                                        </div>
                                        <div className="scrollbar">
                                            <div className="items">
                                                {m_item.str.st['at'].map((a, b) => {
                                                    let test = (
                                                        <Item
                                                            key={b}
                                                            src={a.img}
                                                            text_1={a.name_text}
                                                            text_2={a.as_text}
                                                            number={a.user[m_item.str.form['of']]}
                                                            name={a.name}
                                                            input="at"
                                                            activ_class={m_item.str.input.at_class}
                                                            handleClick={this.props.handleClick}
                                                        />
                                                    );
                                                    if (
                                                        !m_item.str.input['at'] ||
                                                        a.name_text
                                                            .toLowerCase()
                                                            .indexOf(m_item.str.input['at'].toLowerCase()) !== -1
                                                    ) {
                                                        let flag = false;
                                                        for (
                                                            let a_1 = 0, c_1 = m_item.str.st['of'].length;
                                                            a_1 < c_1;
                                                            a_1++
                                                        ) {
                                                            if (
                                                                !m_item.str.input['of'] ||
                                                                m_item.str.st['of'][a_1].name_text
                                                                    .toLowerCase()
                                                                    .indexOf(m_item.str.input['of'].toLowerCase()) !==
                                                                    -1
                                                            ) {
                                                                if (m_item.str.st['of'][a_1].ratio[a.name]) {
                                                                    flag = true;
                                                                    break;
                                                                }
                                                            }
                                                        }
                                                        if (flag) {
                                                            return test;
                                                        } else {
                                                            return '';
                                                        }
                                                    }
                                                    return '';
                                                })}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div>
                    <div
                        className={`my_scrol ${this.props.m_item.str.form.at && this.props.m_item.str.form.at
                            ? ''
                            : 'my_none'}`}
                    >
                        <div className="under-columns">
                            <Form_exchange
                                m_item={m_item.str}
                                onClick={this.props.handleClick}
                                onChange={this.props.handleChange}
                            />
                        </div>
                    </div>
                    <div className="my_class_of_my_scrol_1">
                        <div className="" data-pageame=".HomePageDefault.r31">
                            {my_stor.page[my_stor.lang].HomePageDefault.r31}
                            <br />
                            <Link to="#" className="yellow">
                                {my_stor.page[my_stor.lang].HomePageDefault.r32}
                            </Link>{' '}
                            {my_stor.page[my_stor.lang].HomePageDefault.r33}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default App;



