import React, { Component } from 'react';
import {  Link } from "react-router-dom";
import {  my_url, my_stor } from '../router/My_router';
import Navigator from "./block/comp/stupid_component/navigator/navigator.js";
class SignIn extends Component {
  render() {
    return (
      <main className="page-content sign-page">
        <div className="layout">
          <Navigator
                url_arey={[
                  {
                    to: my_url(""),
                    lun_text:
                     my_stor.page[my_stor.lang].SignIn.r1
                  }
                ]}
                className="breadcrumbs"
                span={my_stor.page[my_stor.lang].SignIn.r2}
              />
          <div className="page-title">{my_stor.page[my_stor.lang].SignIn.r2  }</div>
          <div className="box">
            <form>
              <div className="rows">
                <div className="row">
                  <label htmlFor="email">{my_stor.page[my_stor.lang].SignIn.r3  }</label><br />
                  <input type="email" id="email" placeholder={my_stor.page[my_stor.lang].SignIn.r10  } />
                </div>
                <div className="row">
                  <div className="one-row">
                    <label htmlFor="password">{my_stor.page[my_stor.lang].SignIn.r3  }</label>
                    <Link to="#" className="forgot-password">{my_stor.page[my_stor.lang].SignIn.r4  }</Link>
                    <div className="clear" />
                  </div>
                  <input type="password" id="password" placeholder={my_stor.page[my_stor.lang].SignIn.r5  } />
                </div>
              </div>
              <button type="submit" className="button">{my_stor.page[my_stor.lang].SignIn.r6  }</button>
              <div className="under-button">
              {my_stor.page[my_stor.lang].SignIn.r7  }
              </div>
              <div className="register">
              {my_stor.page[my_stor.lang].SignIn.r8  } <Link to={my_url("SignUp")}  >{my_stor.page[my_stor.lang].SignIn.r9  }</Link>
              </div>
            </form>
          </div>
        </div>
      </main>
    );
  }
}

export default SignIn;



