import React, {Component} from 'react';
// /* import {  Link } from "react-router-dom";
// import {  my_url, my_stor } from "../../../../../router/My_router";

class App extends Component {
    render() {
        let className_1 = this.props.className_1 || 'item',
            className_2 = this.props.className_2 || 'logo',
            className_3 = this.props.className_3 || 'info-wrapper',
            className_4 = this.props.className_4 || 'title',
            className_5 = this.props.className_5 || 'price',
            src = 'pictures/logos/' + this.props.src + '.png' || '',
            srcSet =
                'pictures/logos/' + this.props.src + '@2x.png 2x, pictures/logos/' + this.props.src + '@3x.png 3x' ||
                '',
            text_1 = this.props.text_1 || '',
            text_2 = this.props.text_2 || '',
            number = this.props.number || '';
        return (
            <div
                className={className_1 + ` ${this.props.activ_class === this.props.name ? 'active_currency' : ''}`}
                onClick={this.props.handleClick}
                data-name={this.props.name}
                data-input={this.props.input}
                data-сlick="currency_selection"
            >
                <img
                    alt=""
                    src={src}
                    srcSet={srcSet}
                    className={className_2}
                    data-name={this.props.name}
                    data-input={this.props.input}
                    data-сlick="currency_selection"
                />
                <div
                    className={className_3}
                    data-name={this.props.name}
                    data-input={this.props.input}
                    data-сlick="currency_selection"
                >
                    <span
                        className={className_4}
                        data-name={this.props.name}
                        data-input={this.props.input}
                        data-сlick="currency_selection"
                    >
                        {text_1}
                    </span>
                    <div
                        className={className_5}
                        data-name={this.props.name}
                        data-input={this.props.input}
                        data-сlick="currency_selection"
                    >
                        {text_2}&nbsp;{(() => {
                            if (!number) {
                                return 0;
                            }
                            if (typeof number !== 'number' && /\./gim.test(number)) {
                                return number.replace(/\./gim, ',');
                            } else {
                                if (typeof number === 'boolean') {
                                    return 0;
                                } else {
                                    return number;
                                }
                            }
                        })()}
                    </div>
                </div>
            </div>
        );
    }
}

export default App;



