import React, { Component } from 'react';
import {  Link } from "react-router-dom";
import {  my_url, my_stor } from '../router/My_router';
import Navigator from "./block/comp/stupid_component/navigator/navigator.js";
class SignUp extends Component {
  render() {
    return (
      <main className="page-content sign-page">
      <div className="layout">
        <Navigator
                url_arey={[
                  {
                    to: my_url(""),
                    lun_text:
                    my_stor.page[my_stor.lang].SignUp.r1
                  }
                ]}
                className="breadcrumbs  mobile-padding"
                span={my_stor.page[my_stor.lang].SignUp.r2}
              />
        <div className="page-title">{my_stor.page[my_stor.lang].SignUp.r2  }</div>
        <div className="box">
          <form>
            <div className="rows">
              <div className="row">
                <label htmlFor="email">{my_stor.page[my_stor.lang].SignUp.r3 }</label><br />
                <input type="email" id="email" placeholder={my_stor.page[my_stor.lang].SignUp.r16  } />
              </div>
              <div className="row">
                <div className="one-row">
                  <label htmlFor="password">{my_stor.page[my_stor.lang].SignUp.r4  }</label>
                  <Link to="#" className="forgot-password">{my_stor.page[my_stor.lang].SignUp.r5  }</Link>
                  <div className="clear" />
                </div>
                <input type="password" id="password" placeholder={my_stor.page[my_stor.lang].SignUp.r6  } />
              </div>
              <div className="row">
                <label htmlFor="password1">{my_stor.page[my_stor.lang].SignUp.r7  }</label><br />
                <input type="password" id="password1" placeholder={my_stor.page[my_stor.lang].SignUp.r8  } />
              </div>
            </div>
            <div className="captcha">
              <div className="text">
              {my_stor.page[my_stor.lang].SignUp.r9[0]  }<br />{my_stor.page[my_stor.lang].SignUp.r9[1]}
              </div>
              <div className="recaptcha">
                recaptcha
              </div>
              <div className="clear" />
            </div>
            <div className="agreement">
              <input type="checkbox" />
              {my_stor.page[my_stor.lang].SignUp.r12  } <Link to="#">{my_stor.page[my_stor.lang].SignUp.r10  }</Link>
            </div>
            <button type="submit" className="button">{my_stor.page[my_stor.lang].SignUp.r13  }</button>
            <div className="register">
            {my_stor.page[my_stor.lang].SignUp.r15  } <Link to="#">{my_stor.page[my_stor.lang].SignUp.r14  }</Link>
            </div>
          </form>
        </div>
      </div>
    </main>
    );
  }
}

export default SignUp;



